import * as React from "react";
import "./Highlighter.scss";
import { tokenizeCode } from "../utils/tokenizer";
import { HighLighterProps } from "./HighLighterProps";
import { Line, Token } from "../types/types";
import Clipboard from "./ClipBoard";

const HighLighter = (props: HighLighterProps) => {
  const {
    code,
    style,
    children,
    onCopyToClipboard,
    copyToClipBoard = true,
  } = props;
  //console.log("HighLighter code", code, children);

  const codeToHighLight = code || children || "";

  const tokenize = (code: string): void | React.ReactNode => {
    //let listOfLines: Line[] = superHighlighter(code);
    //console.log("HighLighter code tokenize", code);
    let listOfLines: Line[] = tokenizeCode(code) || [];
    //console.log("HighLighter code listOfLines", listOfLines);
    return (
      <React.Fragment>
        {listOfLines.map((codeLine, index) => (
          <TokenizeSpan2 tokenList={codeLine} key={index} />
        ))}
      </React.Fragment>
    );
  };

  const TokenizeSpan2 = (props: { tokenList: Token[] }): JSX.Element => {
    const { tokenList } = props;
    if (tokenList.length === 0) {
      return (
        <div className="rc-hl-code-line">
          <span className={"none"}> </span>
        </div>
      );
    }
    return (
      <div className="rc-hl-code-line">
        {tokenList.map((currToken: Token, index: number) => (
          <span key={index} className={currToken.category}>
            {currToken.category == "ws" ? " " : currToken.token}
          </span>
        ))}
      </div>
    );
  };

  //const codeHighlighted = tokenize(code || "");
  const handleCopyToClipboard = (_code: string) => {
    onCopyToClipboard?.(_code);
  };
  return (
    <React.Fragment>
      {codeToHighLight && codeToHighLight.length > 0 && (
        <div
          data-testid="highlighter-container"
          style={style}
          className="main-highlighter-relative"
        >
          <div className="highlighter-container-vs-code">
            <code>
              <>{tokenize(codeToHighLight)}</>
            </code>
          </div>
          {copyToClipBoard && (
            <Clipboard
              code={children || code || ""}
              onCopyToClipboard={handleCopyToClipboard}
            />
          )}
        </div>
      )}
    </React.Fragment>
  );
};

export default HighLighter;
