import { Line, Token } from "../types/types";
import { listOfHTMLtags } from "./htmlTags";


const MAX_FUNC_BACK_TOKENS = 4;
let bracketLevel = -1;
let isBracketOpen = false;
const addBracketLevel = () => {
    bracketLevel++;
    if (bracketLevel > 2) {
        bracketLevel = 0;
    }
}
const decBracketLevel = () => {
    bracketLevel--;
    if (bracketLevel < 0) {
        bracketLevel = 2;
    }
}
const highKeywords = [
    "import",
    "from",
    "return",
    "if",
    "else",
    "switch",
    "default",
    "export",
    "as"
]
const keywords = [
    "class",
    "extends",
    "this",
    "super",
    "function",
    "null",
    "var",
    "let",
    "const",
    "typeof",
    "interface",
    "type",
    "false",
    "true",
    "in",
    "private",
    "public",
    "undefined",
];
export const tokens = [
    "keyword",
    "className",
    "punctuation",
    "function",
    "parameter",
    "operator",
    "number",
    "attributeName",
    "default",
];
const punctuation: string[] = [
    ".",
    ",",
    ";",
    ":",
    "<",
    ">",
    "=",
    "!",
    "<",
    ">",
    "/",
    "!",
    "+",
    "*",
    "|",
];
const arrow: string[] = [

    "=>",

]
const openBracket: string[] = [
    "(",
    "{",
    "[",
];
const closeBracket: string[] = [
    ")",
    "}",
    "]",
];
const operatorTwo: string[] = [
    "&&",
    "||",
    "==",
    "!=",
    "<=",
    ">=",
]
const operatorThree: string[] = [
    "===",
    "!==",
]
const openComment = ["//", "/*"];
const closeComment = ["*/"];


export const isOperatorTwo = (word: string) => {
    return operatorTwo.includes(word);
}
export const isOperatorThree = (word: string) => {
    return operatorThree.includes(word);
}
export const isType = (word: string) => {
    return /[A-Z]/.test(word[0]);
};
export const isHighKeyWord = (word: string) => {
    return highKeywords.includes(word);
};
export const isKeyWord = (word: string) => {
    return keywords.includes(word);
};
export const isPunctuation = (word: string) => {
    return punctuation.includes(word);
};
export const isArrow = (word: string) => {
    return arrow.includes(word);
};
export const isOpenBracket = (word: string) => {
    return openBracket.includes(word);
};
export const isCloseBracket = (word: string) => {
    return closeBracket.includes(word);
};


// must rcieve a parameter whether, a string is still open or not
export const tokenizeLine = (
    lineStr: string,
    stringStillOpen?: boolean,
    lastOpenStringChar?: string,
    commentStillOpen?: boolean
): [Line, boolean, string | undefined, boolean] => {
    let tokenArray: Token[] = [];
    let openString: boolean = false;
    let openComment: boolean = false;

    let stringCharToNextCheck: string | undefined = undefined;



    //variable to accumulate chars and split by " " or comment or string
    let acumWord: string = "";
    let continueChecking: boolean = false;
    let lastIndex: number = 0;

    //TO-DO: ask if end of line, so, keep it all line as string
    if (stringStillOpen) {
        openString = true;
        for (let i = 0; i < lineStr.length; i++) {
            let currentChar: string = lineStr[i];

            //check if closes the string with the corresponding last open string character
            if (lastOpenStringChar === currentChar) {

                //check if end of line was reached or there still chars
                //flushes in token
                openString = false;
                acumWord += currentChar;
                tokenArray.push({ token: acumWord, category: "string" });
                acumWord = "";

                if (i < lineStr.length) {
                    continueChecking = true
                    lastIndex = i;
                } else {
                    return [tokenArray, false, undefined, false];
                }

            } else {
                //accumulate in the string token
                acumWord += currentChar;
            }
            ///.....
        }
        ///.....
        ///
    } else if (commentStillOpen) {
        //TO-DO: ask if end of line, so, keep it all line as comment
        openComment = true;
        for (let i = 0; i < lineStr.length; i++) {
            const closeComment1: string = lineStr[i];
            const closeComment2: string = lineStr[i + 1];

            if (closeComment1 === "*" && closeComment2 === "/") {
                //check if end of line was reached or there still chars
                //flushes in token
                openComment = false;
                acumWord += closeComment1 + closeComment2;
                tokenArray.push({ token: acumWord, category: "comment" });
                acumWord = "";
                i++;

                if (i < lineStr.length) {
                    continueChecking = true
                    lastIndex = i;
                } else {
                    return [tokenArray, false, undefined, false];
                }

            } else {
                acumWord += closeComment1;
            }


        }
    }

    const initialIndex = continueChecking ? lastIndex : 0;
    openComment = false;
    openString = false;
    acumWord = "";
    // check for open string
    // comment
    // bracket
    //console.log("tokenizeLine2 tokenArray bf", lineStr, bracketLevel);
    //bracketLevel = -1;
    //console.log("tokenizeLine2 tokenArray bf", lineStr, bracketLevel);
    for (let i = initialIndex; i < lineStr.length; i++) {

        let currentChar: string = lineStr[i];
        //check if open string
        //ask if end of line to finish
        if (["\"", "\'", "\`"].includes(currentChar)) {
            if (acumWord.length > 0) {
                //flushes inmediatelly
                tokenArray.push({ token: acumWord, category: "none" });
                acumWord = "";
            }
            let openStrChar: string = currentChar;

            stringCharToNextCheck = currentChar;
            // from next char, check if it closes the string

            //set string is open to true
            openString = true;
            //better capture the entire string
            acumWord += currentChar;
            let j = i + 1;
            while (1) {
                if (j === lineStr.length) {
                    //end of line?
                    //didnt find the target?
                    tokenArray.push({ token: acumWord, category: "string" });
                    acumWord = "";
                    break;
                }
                const openStrChar2: string = lineStr[j];

                if (openStrChar2 === openStrChar) {
                    //if string is closed in the line
                    acumWord += openStrChar2;
                    openString = false;
                    //flushes token
                    tokenArray.push({ token: acumWord, category: "string" });

                    acumWord = "";
                    //j++;
                    break;
                } else {
                    acumWord += openStrChar2;
                    j++;
                }
            }
            //jump to the last position
            i = j;
            //if stringIsOpen is still true, it means that string was not closed
            // in th entire line form that point
            // so, return the value
            if (openString) {
                return [tokenArray, openString, stringCharToNextCheck, false];
            }
            //return if stil opem
        } else if (currentChar === "/" && lineStr[i + 1] === "*") {
            if (acumWord.length > 0) {
                //flushes inmediatelly
                tokenArray.push({ token: acumWord, category: "none" });
                acumWord = "";
            }
            openComment = true;
            acumWord += currentChar + lineStr[i + 1];
            i = i + 1;
            let j = i + 1;
            while (true) {
                if (j === lineStr.length) {
                    tokenArray.push({ token: acumWord, category: "comment" });
                    acumWord = "";
                    break;
                }

                const closeComment1: string = lineStr[j];
                const closeComment2: string = lineStr[j + 1];

                if (closeComment1 === "*" && closeComment2 === "/") {
                    //if string is closed in the line
                    acumWord += closeComment1 + closeComment2;
                    openComment = false;
                    //flushes token
                    tokenArray.push({ token: acumWord, category: "comment" });

                    acumWord = "";
                    j++;
                    break;
                } else {
                    acumWord += closeComment1;
                    j++;
                }
            }
            //jump to the last position
            i = j;
            if (openComment) {
                return [tokenArray, false, undefined, true];
            }

        } else if (currentChar === "/" && lineStr[i + 1] === "/") {
            if (acumWord.length > 0) {
                //flushes inmediatelly
                tokenArray.push({ token: acumWord, category: "none" });
                acumWord = "";
            }
            //it end in the last character of line yes or yes
            let j = i + 1;
            acumWord += currentChar;
            while (1) {
                if (j === lineStr.length - 1) {
                    //flush
                    acumWord += lineStr[j];
                    tokenArray.push({ token: acumWord, category: "comment" });
                    acumWord = "";
                    break;
                }
                acumWord += lineStr[j];
                j++;
            }
            i = j;
            //it is supposed that "//" comment is in the rest of the line, so line is finished
            break;
        } else if (isArrow(currentChar + lineStr[i + 1])) {
            if (acumWord.length > 0) {
                //flushes
                tokenArray.push({ token: acumWord, category: "none" });
                acumWord = "";
            }
            tokenArray.push({ token: currentChar + lineStr[i + 1], category: "arrow" });
            acumWord = "";
            i++;
        } else if (isOperatorTwo(currentChar + lineStr[i + 1])) {
            if (acumWord.length > 0) {
                //flushes
                tokenArray.push({ token: acumWord, category: "none" });
                acumWord = "";
            }
            tokenArray.push({ token: currentChar + lineStr[i + 1], category: "punctuation" });
            acumWord = "";
            i++;
        } else if (isOperatorThree(currentChar + lineStr[i + 1] + lineStr[i + 2])) {
            if (acumWord.length > 0) {
                //flushes
                tokenArray.push({ token: acumWord, category: "none" });
                acumWord = "";
            }
            tokenArray.push({ token: currentChar + lineStr[i + 1] + lineStr[i + 2], category: "punctuation" });
            acumWord = "";
            i++;
            i++;
        } else if (isPunctuation(currentChar)) {
            if (acumWord.length > 0) {
                //flushes
                tokenArray.push({ token: acumWord, category: "none" });
                acumWord = "";
            }
            tokenArray.push({ token: currentChar, category: "punctuation" });
            acumWord = "";
        } else if (isOpenBracket(currentChar)) {
            if (acumWord.length > 0) {
                //flushes
                tokenArray.push({ token: acumWord, category: "none" });
                acumWord = "";
            }
            addBracketLevel();

            tokenArray.push({ token: currentChar, category: "bracket-" + bracketLevel });
            acumWord = "";
        } else if (isCloseBracket(currentChar)) {
            if (acumWord.length > 0) {
                //flushes
                tokenArray.push({ token: acumWord, category: "none" });
                acumWord = "";
            }


            tokenArray.push({ token: currentChar, category: "bracket-" + bracketLevel });
            decBracketLevel();
            acumWord = "";
        } else {
            //split normally by other reason
            // still accumulating
            acumWord += currentChar;
        }
    }

    if (acumWord.length > 0) {
        tokenArray.push({ token: acumWord, category: "none" });
    }


    //console.log("tokenArray split bf", tokenArray);
    //console.log("tokenArray split bf", tokenArray.map(t => t.token));

    //split by whitespace all non categorized tokens
    tokenArray = tokenArray.reduce((acum: Token[], curr: Token) => {

        if (curr.category === "none") {

            //correction when token is already a whitespace with lenght =1
            // " "
            if (curr.token === " " || curr.token === "") {
                return [...acum, ...[{ token: "", category: "ws" }]];
            } else {
                const splittedStr: string[] = curr.token.split(" ");

                const fixedsplittedStr: string[] = [];
                for (let i = 0; i < splittedStr.length; i++) {
                    const element = splittedStr[i];
                    fixedsplittedStr.push(element);
                    if (element.length > 0 && element !== " " && splittedStr[i + 1]?.length > 0 && splittedStr[i + 1] !== " ") {
                        fixedsplittedStr.push("");
                    }
                }


                const subArrTokens: Token[] = fixedsplittedStr.map((spltStr) => {
                    if (!isNaN(Number(spltStr)) && spltStr.length > 0) {
                        return { token: spltStr, category: "number" }
                    } else if (isHighKeyWord(spltStr)) {
                        return { token: spltStr, category: "high-keyword" }
                    } else if (isKeyWord(spltStr)) {
                        return { token: spltStr, category: "keyword" }
                    } else if (isType(spltStr)) {
                        return { token: spltStr, category: "type" }
                    } else if (spltStr.length === 0 || spltStr === " ") {
                        return { token: "", category: "ws" }
                    } else {
                        return { token: spltStr, category: "none" }
                    }

                })

                return [...acum, ...subArrTokens];
            }

        } else
            return [...acum, curr];
    }, []);

    //console.log("tokenArray split af", tokenArray);
    //console.log("tokenArray split af", tokenArray.map(t => t.token));


    //ws
    tokenArray = tokenArray.map(t => {
        if (t.token.length === 0) {
            return { ...t, category: "ws" }
        } else {
            return { ...t }
        }
    })

    //console.log("tokenizeLine2 tokenArray af", tokenArray);
    //console.log("tokenizeLine2 tokenArray af", tokenArray.map(t => t.token), openString, stringCharToNextCheck, openComment);




    return [tokenArray, openString, stringCharToNextCheck, openComment];
}
export const tokenizeCode = (
    code: string | undefined
): Line[] | undefined => {
    if (!code) {
        return undefined;
    }
    //split in lines
    const lines: string[] = code.split("\n");

    //separate by punctuation
    let tokenizedCode: Line[] = [];

    let stringOpen = false;
    let lastStringOpenChar: string | undefined = undefined;
    let commmentOpen = false;
    //For each line tokenize
    for (let i = 0; i < lines.length; i++) {
        let currentLine: string = lines[i];
        let [tokenArray, isStringOpen, lastChar, isCommentOpen] = tokenizeLine(currentLine,
            stringOpen, lastStringOpenChar, commmentOpen);

        //console.log("tokenArray", tokenArray.map(t => t.token));
        stringOpen = isStringOpen;
        lastStringOpenChar = lastChar;
        commmentOpen = isCommentOpen;

        tokenizedCode.push(tokenArray);
    }

    //first check for functions
    const oneLineCode: Line = tokenizedCode.reduce((acum, curr) => {
        return [...acum, ...curr, { token: "\n", category: "end-line" }]
    }, []);

    //TODO: searchs with whilde should check wheater a Json is open or not 
    // in order to avoid what it find there un til Json is closed
    //since operators "<" or ">" can be found in objects
    for (let i = 0; i < oneLineCode.length; i++) {
        const currToken: Token = oneLineCode[i];
        if (currToken.token === "<") {
            //fix for HTML common tags and Components
            if (oneLineCode[i + 1].category === "type") {
                oneLineCode[i].category = "tag";
                let j = i + 1;
                while (j < oneLineCode.length) {
                    //if I find the close diamond in the oppening tag
                    if (oneLineCode[j].token === ">") {
                        oneLineCode[j].category === "tag";
                        break;
                    }
                    j++;
                }

            } else if (oneLineCode[i + 1].token == ">" || oneLineCode[i + 1].token == "/") {
                oneLineCode[i].category = "tag";
                oneLineCode[i + 1].category = "tag";
                if(oneLineCode[i + 1].token == "/" && listOfHTMLtags.includes(oneLineCode[i + 2].token)){
                    oneLineCode[i + 2].category="keyword";
                }
            } else if (listOfHTMLtags.includes(oneLineCode[i + 1].token)) {
                oneLineCode[i + 1].category = "keyword";
                oneLineCode[i].category = "tag";
                let j = i + 1;
                while (j < oneLineCode.length) {
                    //if I find the close diamond in the oppening tag
                    if (oneLineCode[j].token === ">") {
                        oneLineCode[j].category === "tag";
                        break;
                    }
                    j++;
                }

            } else if (oneLineCode[i + 1].token === "/" && listOfHTMLtags.includes(oneLineCode[i + 2].token) && oneLineCode[i + 3].token === ">") {
                oneLineCode[i + 2].category = "keyword";
                oneLineCode[i].category = "tag";
                oneLineCode[i + 1].category = "tag";
                oneLineCode[i + 3].category = "tag";
            }

        } else if (currToken.token === "/" && oneLineCode[i + 1].token === ">") {
            //fix for HTML common tags and Components
            oneLineCode[i].category = "tag";
            oneLineCode[i + 1].category = "tag";

        } else if (currToken.token === "keyword") {
            //fix for json object with key "type" or other keywords
            if (oneLineCode[i + 1]?.token === ":") {
                currToken.category = "none";
            }
        } else if (currToken.token === "function") {
            //si encuentro la palabra "function"

            if (["none", "type"].includes(oneLineCode[i + 1]?.category))
                oneLineCode[i + 1].category = "function";
            else {
                if (["none", "type"].includes(oneLineCode[i + 2]?.category))
                    oneLineCode[i + 2].category = "function";
            }
        } else if (currToken.token === "(") {
            //si encuentro el open bracket  " ( "
            let j = i - 1;
            //retorcedo hasta encontrar un token con categoria diff from "ws"
            // if I can find the "/>" tokens, I can break it

            let found = false;
            let levelDiamond = 0;
            let maxTokens = 0;
            while (j >= 0 && maxTokens <= MAX_FUNC_BACK_TOKENS) {
                if (oneLineCode[j]?.token === "/" && oneLineCode[j + 1]?.token === ">") {
                    found = false;
                    break;
                } else if (oneLineCode[j]?.category === "none") {
                    found = true;
                    oneLineCode[j].category = "function";
                    break;

                }
                j--;
                maxTokens++;
            }

        }
    }
    for (let i = 0; i < oneLineCode.length; i++) {

        const currToken: Token = oneLineCode[i];
        if (currToken.token === "." && (
            ["none", "function"].includes(oneLineCode[i - 1]?.category)
        )) {
            oneLineCode[i - 1].category = "var";
        }
    }
    //console.log("One Line Code", oneLineCode);
    //console.log("One Line Code", oneLineCode.map(t => t.token));

    let currArray: Line = [];
    tokenizedCode = [];
    oneLineCode.forEach(token => {
        if (token.category === "end-line") {
            tokenizedCode.push(currArray);
            currArray = [];
        } else {
            currArray.push(token);
        }
    });


    return tokenizedCode;
}

